<template>
  <v-container>
    <v-layout text-xs-center wrap>
      

      <v-flex xs12 v-if="!loading">
        <cardDonate/>

        <v-card elevation-19 class="card-radius shadom my-3" v-for="(card, index) in data[0]" :key="index">
          <v-card-title>
            <v-chip class="mb-2 chipB">
              <v-avatar size="35" color="grey">
                <template v-if="card.data.thumbnail.includes('https://')">
                  <v-img :src="card.data.thumbnail"></v-img>
                </template>
              </v-avatar>
              {{card.data.author}}
            </v-chip>
            <br>
            <span class="caption BG">
              {{card.data.title}}
            </span>
          </v-card-title>
          <template v-if="card.data.thumbnail.includes('https://')">
            <img :src="card.data.preview.images[0].source.url.replace('amp;','')" :alt="card.data.title" width="100%">
          </template>
        </v-card>
      </v-flex>
      <v-flex xs12 mt-5 v-else>
        <div class="flex justify-center align-center">
          <v-progress-circular :size="70" :width="7" color="purple" indeterminate></v-progress-circular>
        </div>
        <span class="BG" v-if="noInternet">
          No Internet
        </span>
      </v-flex>

      <v-flex xs12 justify-center>
        <img src="/img/memes.jpg" alt="FreeMemesKids" width="100%" class="round-img shadom">
        <v-btn color="info" round @click="carga()" v-if="!loadingMore">More</v-btn>
        <v-progress-circular :size="30" :width="7" color="purple" indeterminate v-else></v-progress-circular>
        <div>
          <span class="BG" v-if="noInternetMore">No Internet</span>
        </div>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import cardDonate from './cardDonate.vue'
  export default {
    components: {
      cardDonate
    },
    props: ['url'],
    data: () => ({
      data: [],
      loading: true,
      noInternet:false,

      loadingMore:false,
      noInternetMore:false,
    }),

    created() {
      
      fetch(this.url)
        .then(function (response) {
          return response.json();
        }).then((x) => {
          this.data.push(x.data.children)
          this.loading = false
          this.noInternet = false
        }).catch(() => {
          this.noInternet = true;
        })
    },
    methods: {
      carga(){
        this.loadingMore = true;
        fetch(this.url)
        .then(function (response) {
          return response.json();
        }).then((x) => {
          x.data.children.forEach(element => {
            this.data[0].push(element)
          });
          this.loadingMore = false;
          this.noInternetMore = false;
        }).catch(() => {
          this.noInternetMore = true;
        })
      }
    },
  }
</script>

<style css>
  .card-radius {
    border-radius: 20px;
  }

  .shadom {
    box-shadow: 1px 10px 20px 2px rgba(213, 0, 255), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12)
  }

  .chipB {
    background: #DA1FF2
  }
  .round-img{
    border-radius: 5px
  }
</style>